import React from 'react'
import moment from 'moment'

const CommentSummary = ({comment}) => {
    return (
        <div className="card z-depth-0 comment-summary">
            <div className="card-content grey-text text-darken-3">
                <span className="card-title">{comment.title}</span>
                <p>Posted by {comment.authorFirstName} {comment.authorLastName}</p>
                <p className="grey-text">{moment(comment.createdAt.toDate()).calendar()}</p>
            </div>
        </div>

    )
}

export default CommentSummary;