const initState = {
    authError: null
}

const authReducer = (state = initState, action) => {
    switch(action.type){
        case 'LOGIN_ERROR':
            return {
                ...state,
                authError: 'Login Failed. Invalid Email or Password'
            }
        case 'LOGIN_SUCCESS':
            console.log('login success')
            return {
                ...state,
                authError: null
            }
        case 'SIGNOUT_SUCCESS':
            console.log('signout success')
            return state;
        case 'SIGNUP_SUCCESS'  :
             console.log('signup success') 
             return {
                 ...state,
                 authError: null
             }
        case 'SIGNUP_ERROR':
            console.log('signup error')
            return {
                ...state,
                authError: action.err.message
            }
        case 'UPDATE_SUCCESS':
            console.log('update success')
            return{
                ...state,
                authError: null
            }
        case 'UPDATE_ERROR':
            console.log('update error')
            return{
                ...state,
                authError: 'Update failed'
            }
        default:
            return state;
    }
   
}

export default authReducer